'use client'

import { useEffect } from 'react'

interface GlobalErrorProps {
    error: Error & { digest?: string }
    reset: () => void
}

/**
 * Next.js global error boundary for the root layout.
 * This catches errors that occur in the root layout itself.
 * Must include its own <html> and <body> tags.
 */
export default function GlobalError({ error, reset }: GlobalErrorProps) {
    useEffect(() => {
        // Log to console in global error (logger might not be available)
        console.error('Global error:', error)
    }, [error])

    return (
        <html lang="en">
            <body className="bg-white dark:bg-gray-900">
                <div className="flex flex-col items-center justify-center min-h-screen p-8 text-center">
                    <div className="rounded-full bg-red-100 dark:bg-red-900/20 p-6 mb-6">
                        <svg
                            className="h-12 w-12 text-red-600 dark:text-red-400"
                            fill="none"
                            viewBox="0 0 24 24"
                            stroke="currentColor"
                        >
                            <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth={2}
                                d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z"
                            />
                        </svg>
                    </div>

                    <h1 className="text-3xl font-bold mb-2 text-gray-900 dark:text-white">
                        Critical Error
                    </h1>
                    <p className="text-gray-600 dark:text-gray-400 mb-6 max-w-md">
                        A critical error occurred. Please refresh the page or try again later.
                    </p>

                    <button
                        onClick={reset}
                        className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
                    >
                        Try again
                    </button>
                </div>
            </body>
        </html>
    )
}
